import {required} from "vuelidate/lib/validators";

export default {
  layout: 'AdminTemplate',
  name: "university"
  ,
  data() {
    return {
      editId: 0,
      header_title:'',
      overlay_show: false ,
      tabVisible: 0,
      pageIndex: 1,
      search_visible: true, add_visible: false,
      nData: {
        id: 0,
        name: "",
        emailPattern: "",
        email: "",
        isenable: true,
      },
      active_options:[
        { value: 2, text: 'All' },
        { value: 0, text: 'Disable' },
        { value: 1, text: 'Enable' },
      ],
      t_fields:[
        {
          key: 'name',
          label: 'Name',
          sortable: true
        },
        {
          key: 'emailPattern',
          label: 'Email Pattern',
          sortable: false,

        },
        {
          key: 'email',
          label: 'Email',
          sortable: false,
        },
        {
          key: 'status',
          label: 'Status',
          sortable: false,
        },
        {
          key: 'actions',
          label: 'Edit',
          sortable: false
        },
      ],
      t_items: []
    }
  }
  ,
  mounted()
  {
    this.loadUniversity()
  },

  validations: {
    nData:{
      name:{
        required,
      },
      email:{
        required,
      },
      emailPattern:{
        required,
      }
    }
  },

  methods:
    {
      showAddModal() {
        this.editId = 0
        this.nData.id = 0
        this.nData.emailPattern = ""
        this.nData.name = ""
        this.nData.isenable = true
        this.nData.email = ""
        this.$refs['edit-modal'].show()
      },
      showEditModal(item) {
        this.editId = item.id
        this.nData.id = item.id
        this.nData.emailPattern = item.emailPattern
        this.nData.name = item.name
        this.nData.isenable = item.isenable
        this.nData.email = item.email

        this.$refs['edit-modal'].show()
      },
      hideModal(name) {
        this.editId = 0
        this.$refs[name].hide()
      },
      makeToast(variant = 'success', header = 'Message', msg = '')
      {
        this.$bvToast.toast(this.$t(msg), {
          title: header,
          variant: variant,
          solid: true
        })
      },
      save(evt)
      {
        this.$v.$reset()
        this.errors = null
        evt.preventDefault()
        this.$v.nData.$touch()
        if (this.$v.nData.$invalid) {
          console.log("vuelidate error")
        } else {
          // add new record
          if (this.nData.id === 0) {
            this.$axios.post('/definitions/createUny', this.nData).then(
              rsp => {
                //this.t_items = rsp.data
                this.t_items.push({ id: rsp.data.id , name: rsp.data.name, emailPattern: rsp.data.emailPattern  ,
                  email: rsp.data.email ,  isenable: rsp.data.isenable ? "Enable" : "Disable"})
                this.hideModal('edit-modal')
                this.makeToast("success" , "Success" , this.$t("done_successfully" ))

              })

          }
          else
          {
            this.$axios.post('/definitions/editUny', this.nData).then(
              rsp => {
                this.hideModal('edit-modal')
                this.makeToast("success" , "Success" , this.$t("done_successfully" ))
                this.loadUniversity()
              })
          }
        }
      },
      cancel(){
        this.editId = 0
        this.nData.id = 0
        this.hideModal('edit-modal')
      },
      loadUniversity()
      {
        this.$axios.post('/definitions/universityList', this.searchData).then((res) => {
          this.t_items = res.data
          this.overlay_show = false;
        }).catch(error => {
          this.overlay_show = false;
          this.makeToast("danger" , "Error" , this.$t("search_error") )
          console.log(error)
        });
      }

    }

}
